<template>
	<view class="container">
		<view class="questForm">
			<text class="questTitle">{{questObj.title}}</text>
			<view class="formList">
				<view class="formItem" v-for="(item,index) in queList" :key="index">
					<view class="itemTitle">
						<text v-if="item.isMust==1">*</text>
						{{item.title}}<text v-if="item.remark">（{{item.remark}}）</text>
					</view>
					<u-input v-model="item.salesInput" v-if="item.type==2" />
					<template v-if="item.type==1">
						<u-radio-group :wrap="true" v-model="item.checked" size="34" v-if="item.selection==1">
							<u-radio v-for="(item2, radioIndex) in item.options" :key="radioIndex" :name="item2.key" active-color="#03958C">
								{{ item2.value }}
							</u-radio>
						</u-radio-group>
						<checkbox-group @change="checkboxGroupChange($event,item)" v-else>
							<view class="checkList" v-for="item3 in item.options" :key="item3.key">
								<checkbox :value="item3.key" :checked="item3.checked" color="#03958C" style="transform:scale(0.6)" />
								<text>{{item3.value}}</text>
							</view>
						</checkbox-group>
					</template>
				</view>
			</view>
		</view>
		<view class="submitBtn" @click="submitEvent">提交问卷</view>
	</view>
</template>
<script>
	import httpUtils from '@/utils/httpUtils.js';
	import Tools from '@/utils/tools.js';
	export default {
		data() {
			return {
				isshow: false,
				cusId: '', // 客户id
				questObj: {},
				queList: [], // 试卷列表
			}
		},
		onLoad(options) {
			this.cusId = options.cusId
			this._getList(options.id)
		},
		methods: {
			_getList(id) {
				Tools.showLoading('加载中')
				let that = this
				httpUtils.sendHttpRequest('5002', Object.assign({}, {questionnaireId: id}), function(res) {
					Tools.hideLoading()
					if (res.data.code == '0') {
						that.questObj = res.data.data
						res.data.data.content.map((item, index) => {
							if (item.type == 1 && item.selection == 2) {
								item.checked = item.checked ? item.checked.split(',') : []
								item.options.map(v => v.checked = false)
							}
						})
						that.queList = res.data.data.content
						that.isshow = true
					}
				})
			},
			// 多选
			checkboxGroupChange(e,item){
			 item.checked = e.detail.value.join(',')
			},
			// 提交问卷
			submitEvent() {
				const error = this.queList.some(item => item.isMust == 1 && item.type == 2 && item.salesInput == '');
				const error2 = this.queList.some(item => item.isMust == 1 && item.type == 1 && item.checked == '');
				if (error || error2) {
					Tools.showErrorToast(error ? '请输入必填项' : '请选择必填项')
				} else {
					this.questObj.salesOrderId = this.cusId
					Tools.showLoading('提交中')
					httpUtils.sendHttpRequest('5003', Object.assign({}, {json: this.questObj}), function(res) {
						Tools.hideLoading()
						if (res.data.code == '0') {
							Tools.showSuccessToast('提交成功')
							setTimeout(function() {
								const pages = getCurrentPages();
								if (pages.length > 1) {
									let beforePage = pages[pages.length - 2]; 
									beforePage.$vm._refresh();
								 }
								 uni.navigateBack({
								 	delta : 1
								 })
							}, 500)
						}
					})
				}
			}
		}
	}
</script>

<style lang="scss">
	page {
		height: 100%;
		background: linear-gradient(135deg, #03958C, #029F7C);
	}

	.container {
		height: 100%;

		.questForm {
			margin: 0 30rpx;
			padding: 0 40rpx;
			background-color: #fff;
			height: 98%;
			border-radius: 20rpx;

			.questTitle {
				width: 100%;
				text-align: center;
				padding: 30rpx 30rpx 16rpx;
				font-size: 34rpx;
				font-weight: bold;
				display: inline-block;
			}

			.formList {
				width: 100%;
				max-height: 82%;
				overflow-y: scroll;
				display: inline-block;

				.formItem {
					.itemTitle {
						font-size: 30rpx;
						font-weight: bold;
						padding: 16rpx 0;

						text {
							color: #F86E6E;
							margin-right: 8rpx;
						}
					}
				}

				.u-radio {
					margin-bottom: 6rpx;

					.u-radio__label {
						padding-left: 10rpx;
						font-size: 28rpx;
					}
				}

				.u-input__input {
					border-bottom: 1rpx solid #e5e5e5;
				}

				.checkList {
					display: flex;
					align-items: center;
					line-height: 52rpx;

					checkbox {
						margin-left: -8rpx;
					}
				}

				.u-form-item--left__content__label {
					font-weight: bold;
					font-size: 30rpx;
				}
			}
		}

		.submitBtn {
			position: fixed;
			bottom: 60rpx;
			left: 95rpx;
			width: 560rpx;
			height: 88rpx;
			background: linear-gradient(90deg, #03958C, #00AE66);
			color: #fff;
			border-radius: 50rpx;
			line-height: 88rpx;
			text-align: center;
			font-size: 30rpx;
		}
	}
</style>
